@import url(app.css);

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}

.modal__content {
  width: 90%;
  max-width: 500px;
  padding: 2rem;
  border-radius: 10px;
  background-color: #1e375a;
  color: #fff;
  text-align: center;
}

.modal__content h2 {
  margin-bottom: 1rem;
  color: var(--light-blue);
}

.modal__content .btn {
  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  background-color: blue;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.register-form .form-input1 {
  padding: 10px;
  margin-bottom: 4vw;
  border: 1px solid #767676 !important;
  border-radius: 5px;
}

.close-btn {
  position: relative;
  top: -10%;
  right:-40%;
  font-size: 1.5rem;
  color: black;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .modal__content {
    padding: 1rem;
  }
}

.book {
  width: 100%;
}
